import Add from 'components/pages/Add'
import Home from 'components/pages/Home'
import Play from 'components/pages/Play';
import { Component } from 'react'
import { Route } from 'react-router'

class Main extends Component {
  render() {
    return (
      <main className="Main">
        <Route path="/home">
          <Home />
        </Route>
        <Route path="/add">
          <Add />
        </Route>
        <Route path="/play">
          <Play />
        </Route>
      </main>
    );
  }
}

export default Main;
