import CurrentClass from 'currentClass';
import { Component, useContext } from 'react'
import { Link } from 'react-router-dom';

function NavLink(props) {
  const anim = useContext(CurrentClass);

  return (
    <Link
      to={props.to}
      className="NavToggle__link"
      onClick={() => anim.reset('animate-close')}
    >
      {props.children}
    </Link>
  );
}

class NavToggle extends Component {
  close = () => {
    this.context.reset('animate-close');
  };

  render() {
    return (
      <nav className={`NavToggle ${this.context.currentClass}`}>
        <NavLink to="/home">
          <i className="fas fa-home"></i> Home
        </NavLink>
        <NavLink to="/add">
          <i className="fas fa-plus-square"></i> Add
        </NavLink>
        <NavLink to="/play">
          <i className="fas fa-play-circle"></i> Play
        </NavLink>
        <i
          className="fas fa-times"
          onClick={this.close}
        ></i>
      </nav>
    );
  }
}

NavToggle.contextType = CurrentClass;

export default NavToggle;
