import AlertContext from "alertContext";
import { Field, Form, Formik } from "formik";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router";
import { toggle } from "slices/darkSlice";
import { addQuestion } from "slices/questionsSlice";

export class Add extends Component {
  state = {
    correct: 1,
  };

  form = {
    initialValues: {
      text: "",
      variant_1: "",
      variant_2: "",
      variant_3: "",
    },
    validate: (v) => {},
    onSubmit: (v) => {
      if (
        v.text === "" ||
        v.variant_1 === "" ||
        v.variant_2 === "" ||
        v.variant_3 === ""
      ) {
        this.context.resetMessage("Empty values!");
        this.context.resetClass("anim-alert-down");
        this.props.toggle();
        return;
      }
      const q = {
        text: v.text,
        variants: [v.variant_1, v.variant_2, v.variant_3],
        correct: this.state.correct,
      };
      this.props.add(q);
      this.props.history.push('/home');
    },
  };

  setCorrect = (c) => {
    this.setState({ correct: c });
  };

  render() {
    return (
      <div className="Add">
        <h1>Add</h1>
        <Formik {...this.form}>
          <Form className="Add__form">
            <Field
              name="text"
              placeholder="Question"
              as="textarea"
              className="form__field-question"
            />
            <br />
            <div className="form__variant-wrapper">
              <Field
                name="variant_1"
                placeholder="Variant 1"
                className="form__variant-input"
              />
              <div
                className="form__variant-tick"
                onClick={() => this.setCorrect(0)}
              >
                {this.state.correct === 0 ? (
                  <i className="fas fa-check"></i>
                ) : null}
              </div>
            </div>
            <div className="form__variant-wrapper">
              <Field
                name="variant_2"
                placeholder="Variant 2"
                className="form__variant-input"
              />
              <div
                className="form__variant-tick"
                onClick={() => this.setCorrect(1)}
              >
                {this.state.correct === 1 ? (
                  <i className="fas fa-check"></i>
                ) : null}
              </div>
            </div>
            <div className="form__variant-wrapper">
              <Field
                name="variant_3"
                placeholder="Variant 3"
                className="form__variant-input"
              />
              <div
                className="form__variant-tick"
                onClick={() => this.setCorrect(2)}
              >
                {this.state.correct === 2 ? (
                  <i className="fas fa-check"></i>
                ) : null}
              </div>
            </div>
            <br />
            <input type="submit" value="Create" className="form__submit" />
          </Form>
        </Formik>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({});

const mapDispatchToProps = (dispatch) => {
  return {
    add(payload) {
      dispatch(addQuestion(payload));
    },
    toggle() {
      dispatch(toggle());
    }
  };
};

Add.contextType = AlertContext;

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Add));
