const { createSlice } = require("@reduxjs/toolkit");

const countSlice = createSlice({
  name: 'questions',
  initialState: [
    {
      text: 'What is the capital of Great Britial',
      variants: ['London', 'New York', 'Bukhara'],
      correct: 0
    },
    {
      text: 'Who is Daler',
      variants: ['Teacher', 'Programmer', 'Both'],
      correct: 1
    }
  ],
  reducers: {
    addQuestion(state, { payload }) {
      state.push(payload);
    }
  }
});

export const { addQuestion } = countSlice.actions;

export default countSlice.reducer;
