import React, { Component } from "react";
import { connect } from "react-redux";

class Play extends Component {
  state = {
    current: 0,
    correct: 0,
    show: "questions",
  };

  playAgain = () => {
    this.setState({
      current: 0,
      correct: 0,
      show: 'questions'
    });
  };

  incrementCorrect = () => {
    this.setState((state) => ({ correct: state.correct + 1 }));
  };

  incrementCurrent = () => {
    this.setState((state) => ({ current: state.current + 1 }));
  };

  toggleShow = () => {
    this.setState((state) => {
      if (state.show === 'questions') {
        return {
          show: 'results'
        };
      } else {
        return {
          show: 'questions'
        };
      }
    });
  };

  handleClick = (i) => {
    const currentQ = this.props.questions[this.state.current];
    if (currentQ.correct === i) {
      this.incrementCorrect();
    }
    if (this.props.questions.length !== this.state.current + 1) {
      this.incrementCurrent();
    } else {
      this.toggleShow();
    }
  };

  render() {
    const questions = this.props.questions;
    const currentQuestion = questions[this.state.current];

    return (
      <div className="Play">
        <h1>Play</h1>
        {this.state.show === "questions" ? (
          <div className="Play__question">
            <div className="Play__question__text">{currentQuestion.text}</div>
            <ul className="Play__question__variants">
              <li
                className="Play__question__variant"
                onClick={() => this.handleClick(0)}
              >
                {currentQuestion.variants[0]}
              </li>
              <li
                className="Play__question__variant"
                onClick={() => this.handleClick(1)}
              >
                {currentQuestion.variants[1]}
              </li>
              <li
                className="Play__question__variant"
                onClick={() => this.handleClick(2)}
              >
                {currentQuestion.variants[2]}
              </li>
            </ul>
          </div>
        ) : (
          <div className="Play__results">
            <h2>
              {this.state.correct}/{questions.length} <br/>
              Great!
            </h2>
            <button
              className="results__play-again"
              onClick={this.playAgain}
            >
              Play again
            </button>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  questions: state.questions,
});

const mapDispatchToProps = {};

export default connect(mapStateToProps, mapDispatchToProps)(Play);
