import React, { Component } from "react";
import PropTypes from "prop-types";

export class Question extends Component {
  render() {
    const variants = this.props.variants.map((variant) => (
      <li
        className={`question__variant
          ${
            this.props.variants.indexOf(variant) === this.props.correct
              ? "question__variant-correct"
              : "question__variant-incorrect"
          }`}
        key={variant}
      >
        {variant}
        {this.props.variants.indexOf(variant) === this.props.correct ? (
          <i className="fas fa-check"></i>
        ) : null}
      </li>
    ));

    return (
      <div className="Questions__question">
        <div className="question__text">{this.props.children}</div>
        <h2>Variants:</h2>
        <ul className="question__variants">{variants}</ul>
      </div>
    );
  }
}

Question.propTypes = {
  variants: PropTypes.array.isRequired,
  correct: PropTypes.number.isRequired,
};

export default Question;
