const { createSlice } = require("@reduxjs/toolkit");

const darkSlice = createSlice({
  name: 'dark',
  initialState: {
    isDark: false
  },
  reducers: {
    toggle(state) {
      state.isDark = !state.isDark;
    }
  }
});

export const { toggle } = darkSlice.actions;

export default darkSlice.reducer;
